@import "../fonts/Dijkstra/dijkstra.css";@import "../fonts/Celestia/celestia.css";@import "../fonts/Inconsolata/inconsolata.css";body{font-family:'Dijkstra';font-size:36pt;font-weight:200;letter-spacing:-0.02em;color:#232333;background:#f0f0f0}#slides section{text-align:center;background:rgba(240,240,240,0.85)}#slides pre{text-align:left;letter-spacing:0}#slides h1,#slides h2,#slides h3,#slides h4,#slides h5,#slides h6{background:none;margin:0 0 20px 0;padding:0;border:0;font-size:100%;vertical-align:baseline;color:#603a60;line-height:1.05em;letter-spacing:.02em;font-family:'Celestia';text-transform:uppercase;text-shadow:0 0 6px rgba(0,0,0,0.2);-webkit-hyphens:auto;-moz-hyphens:auto;hyphens:auto;word-wrap:break-word;line-height:1}#slides h1{font-size:3.77em;text-shadow:0 1px 0 #503050,0 2px 0 #4d2e4d,0 3px 0 #4a2d4a,0 4px 0 #472b47,0 5px 0 #432943,0 6px 1px rgba(0,0,0,0.1),0 0 5px rgba(0,0,0,0.1),0 1px 3px rgba(0,0,0,0.3),0 3px 5px rgba(0,0,0,0.2),0 5px 10px rgba(0,0,0,0.25),0 20px 20px rgba(0,0,0,0.15)}#slides h2{font-size:2.11em;text-shadow:0 1px 0 #503050,0 2px 0 #4d2e4d,0 3px 0 #4a2d4a,0 4px 0 #472b47,0 6px 1px rgba(0,0,0,0.1),0 0 5px rgba(0,0,0,0.1),0 1px 3px rgba(0,0,0,0.3),0 3px 5px rgba(0,0,0,0.2),0 4px 8px rgba(0,0,0,0.25),0 10px 10px rgba(0,0,0,0.15)}#slides h3{font-size:1.55em;text-shadow:0 1px 0 #503050,0 2px 0 #4d2e4d,0 6px 1px rgba(0,0,0,0.1),0 0 5px rgba(0,0,0,0.1),0 1px 3px rgba(0,0,0,0.3),0 3px 5px rgba(0,0,0,0.2),0 5px 10px rgba(0,0,0,0.25)}#slides h4,#slides h5,#slides h6{font-size:1em;text-shadow:0 1px 0 #503050,0 6px 1px rgba(0,0,0,0.1),0 0 5px rgba(0,0,0,0.1),0 1px 3px rgba(0,0,0,0.3),0 3px 5px rgba(0,0,0,0.2)}#slides p,#slides li,#slides blockquote{text-shadow:1px 1px 4px rgba(35,35,51,0.4);line-height:48pt}#slides strong{letter-spacing:.05em}#slides blockquote{background:#d4d4d4}#slides a:not(.image){color:#0f2050;text-decoration:underline;text-shadow:1px 1px 4px rgba(15,32,80,0.5);-webkit-transition:all .15s ease;-moz-transition:all .15s ease;-ms-transition:all .15s ease;-o-transition:all .15s ease;transition:all .15s ease}#slides a:not(.image):hover{color:#1f42a6;text-shadow:1px 1px 4px rgba(31,66,166,0.75)}#slides ol{counter-reset:li}#slides ol li:before{content:counters(li, " ", arabic-indic);counter-increment:li}#slides ul li:before{content:'✤'}#slides ul,#slides ol{margin:.3em 0}#slides ul li,#slides ol li{list-style:none;position:relative}#slides ul li:before,#slides ol li:before{color:#8d8d95;text-align:right;position:absolute;left:0;width:2em;margin-left:-2.3em;text-shadow:0 2px 2px rgba(141,141,149,0.6)}